
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

#tree[data-v-0414a5fa] {
  display: block;
  position: relative;
  padding: 5px 15px;
}
#tree ul[data-v-0414a5fa] {
  position: relative;
  padding-left: 60px;
  margin: 0;
}
#tree ul > li[data-v-0414a5fa] {
  position: relative;
  padding: 5px 0;
}
#tree > ul[data-v-0414a5fa] {
  padding: 0;
  margin: 0;
}
/* 水平线 */
#tree > ul ul > li[data-v-0414a5fa]:after {
  content: "  ";
  position: absolute;
  top: 30px;
  left: -45px;
  width: 45px;
  border-top: 1px solid #ddd;
}
/** 垂直方向连线 */
#tree ul > li[data-v-0414a5fa]:not(:last-child):before {
  content: " ";
  position: absolute;
  top: 0;
  left: -45px;
  height: 100%;
  border: none;
  border-left: 1px solid #ddd;
}
#tree ul > li[data-v-0414a5fa]:last-child:before {
  content: " ";
  position: absolute;
  top: 0;
  left: -45px;
  height: 30px;
  border: none;
  border-left: 1px solid #ddd;
}
ul[data-v-0414a5fa] {
  list-style-type: none;
  text-align: left;
}
span[data-v-0414a5fa] {
  display: inline-block;
  text-align: center;
  height: 45px;
  color: #888;
  line-height: 45px;
  border: 1px solid #ddd;
  border-radius: 5px;
  padding: 0 8px;
  font-size: 1.5em;
  cursor: pointer;
}
/* 获得焦点改变背景色 */
#tree span[data-v-0414a5fa]:hover,
#tree span:hover + ul span[data-v-0414a5fa] {
  color: #fff;
  background-color: deepskyblue;
}
/* 获得焦点改变边框和连线的颜色 */
#tree span[data-v-0414a5fa]:hover,
#tree span:hover + ul span[data-v-0414a5fa],
#tree span:hover + ul li[data-v-0414a5fa]:before,
#tree span:hover + ul li[data-v-0414a5fa]:after {
  border-color: blue;
}

